import React, { Component } from 'react';
import '../App.css';
import { withStyles, createStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import DvrIcon from '@mui/icons-material/Dvr';
import MovieFilterIcon from '@mui/icons-material/MovieFilter';

const styles = createStyles({
  subTitle: {
    fontSize: '24px',
    fontWeight: 800,
    color: '#555555',
    textDecoration: 'none',
    fontFamily: 'Gravity',
    marginTop: '16px',
  },
  contentText1: {
    fontSize: '16px',
    lineHeight: 1.2,
    // paddingRight: '40px',
    fontFamily: 'Gravity',
  },
  contentText2: {
    fontSize: '18px',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  contentNode: {
    margin: '0 0 0px 0',
    fontFamily: 'Gravity',
  },
  projectTeaserContainer: {
    width: '100%',
    height: '90px',
    borderRadius: '4px',
    boxShadow: '#00000024 0px 0px 4px 2px',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  projectTeaser: {
    width: '100%',
    height: '100%',
    objectFit: "cover",
    fontFamily: 'Gravity',
    userSelect: 'none',
    userDrag: 'none',
  },
  projectTitle: {
    fontWeight: 800,
    fontSize: '16px',
    color: '#353535',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  projectAuthors: {
    color: '#555555',
    lineHeight: 1.2,
    display: 'flex',
    flexWrap: 'wrap',
    fontFamily: 'Gravity',
    fontSize: '12px',
  },
  projectIcons: {
    color: '#555555',
    lineHeight: 1,
    display: 'flex',
    flexWrap: 'wrap',
    fontFamily: 'Gravity',
  },
  projectDescription: {
    color: '#848484',
    fontStyle: 'italic',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
    fontSize: '10px',
  },
  year: {
    color: '#555555',
    fontStyle: 'italic',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
    fontSize: '16px',
    marginTop: '10px'
  },
  projectIconText: {
    fontSize: '12px',
    margin: '0 20px 0 0',
    position: "relative",
    top: '-6px',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
});

type Props = {
  classes: {
    [propName: string]: string
  }
}

class Project extends Component<Props> {
  state = {
    projectsByYear: [
      {
        year: 2022,
        projects: [
          {
            title: 'Imagination to the Universe: Mingling the Ancient and the Present',
            author: ['Junxiu Tang', 'Yifang Wang', 'Jiayi Zhou', 'Xinhuan Shu', 'Tan Tang', 'Huamin Qu', 'Yingcai Wu'],
            teaser: './project/fstmgs.png',
            description: 'An ongoing AR project. The series of works unscramble the Chinese\'s everlasting exploration of the universe based on a triple Mingling Space.',
            demoVideoLink: 'https://www.bilibili.com/video/BV17W4y1C7J9/?vd_source=a76eec98ce5cfba929c3660c26f4d211',
            url: 'https://fs-tm-gs.github.io/',
            year: 2022,
          },
        ]
      },
      {
        year: 2021,
        projects: [
          {
            title: 'Explore Mindfulness without Deflection: A Data Art Based on the Books of Songs',
            author: ['Yifang Wang', 'Yifan Cao', 'Junxiu Tang', 'Yang Wang', 'Huamin Qu', 'Yingcai Wu'],
            teaser: './project/vis21-shijing.png',
            description: 'A VISAP 2021 Pictorial.',
            url: 'http://wangyifang.top/the-book-of-songs/',
            demoVideoLink: 'https://www.youtube.com/watch?v=5fNTfPpeXQs',
            year: 2021,
          },
        ]
      },
      {
        year: 'Before 2019',
        projects: [
          {
            title: 'A Gear-storyline of Inception',
            author: ['Junxiu Tang', 'Ruixin Liu', 'Xinhao Zhuang'],
            teaser: './project/storyline.png',
            description: 'An interactive storyline of the movie "Inception". This work uses gears as a metaphor for the movie narrative. Exhibited in China Vis Art Program 2018. The initial version of this work is the homework in the Cross Media Visualization course.',
            url: '',
            demoVideoLink: 'https://youtu.be/K363iQM6elI',
            year: 2018,
          },
          {
            title: 'Colorful (Do What You Want)',
            author: ['Junxiu Tang'],
            teaser: './project/animation.png',
            description: 'A stop-motion animation that tells people to break the mold and pursue what they want. The homework in the Digital Media Design course.',
            url: '',
            demoVideoLink: 'https://youtu.be/HLhUustrx8c',
            year: 2018,
          },
        ]
      },
    ]
  }

  render() {
    const { classes } = this.props;
    const { projectsByYear } = this.state;

    const projectNode =
      <div>
        <Grid container spacing={2}>
          <Grid item md={8}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <div className={classes.subTitle}>{`# Project`}</div>
            </Typography>
          </Grid>
        </Grid>
        {projectsByYear.map((projByYr) =>
          projByYr.projects.length > 0 ?
            <div>
              <Typography variant="subtitle1" gutterBottom align="left" className={classes.year}>
                {projByYr.year}
              </Typography>
              {projByYr.projects.map((proj) =>
                <Grid container spacing={4} justify="center" alignItems="center" className={classes.contentNode}>
                  <Grid item md={3}>
                    <div className={classes.projectTeaserContainer}>
                      <img alt={proj.title} src={proj.teaser} className={classes.projectTeaser} />
                    </div>
                  </Grid>
                  <Grid item md={9}>
                    <Typography variant="subtitle1" gutterBottom align="left" className={classes.projectTitle}>
                      {proj.title} ({proj.year})
                    </Typography>
                    <Typography variant="subtitle1" gutterBottom align="left" className={classes.projectAuthors}>
                      {proj.author.map((a, i) => {
                        const text = `${a}${i === proj.author.length - 1 ? '' : ', '}`;
                        return a === "Junxiu Tang" ?
                          <div style={{ fontWeight: 800, marginLeft: `${ i === 0 ? 0 : 4}px` }}>{text}</div> :
                          <div style={{ marginLeft: `${ i === 0 ? 0 : 4}px` }}>{text}</div>
                      })}
                    </Typography>
                    <Typography variant="subtitle1" gutterBottom align="left" className={classes.projectDescription}>
                      {proj.description}
                    </Typography>
                    <Typography variant="caption" gutterBottom align="left" className={classes.projectIcons}>
                      <Grid item md={6}>
                        {proj.url === "" ? null : (<React.Fragment><a href={proj.url}><DvrIcon fontSize='small' /> <span className={classes.projectIconText}>page</span></a></React.Fragment>)}
                        {proj.demoVideoLink === "" ? null : (<React.Fragment><a href={proj.demoVideoLink}><MovieFilterIcon fontSize='small' /> <span className={classes.projectIconText}>video</span></a></React.Fragment>)}
                      </Grid>
                    </Typography>
                  </Grid>
                </Grid>
              )}
            </div>

            : null
        )}
      </div>
    return (
      <div className="project" id="project">
        {projectNode}
      </div>
    )
  }
}

export default withStyles(styles)(Project);
