import React, { Component } from 'react';
import '../App.css';
import { withStyles, createStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import MenuBookIcon from '@mui/icons-material/MenuBook';

const styles = createStyles({
  subTitle: {
    fontSize: '24px',
    fontWeight: 800,
    color: '#555555',
    textDecoration: 'none',
    fontFamily: 'Gravity',
    marginTop: '16px',
  },
  contentText1: {
    fontSize: '14px',
    lineHeight: 1.2,
    // paddingRight: '40px',
    fontFamily: 'Gravity',
  },
  contentText2: {
    fontSize: '16px',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  educationLogo: {
    width: '100%',
  },
  contentNode: {
    margin: '0 0 0px 0',
    justifyContent: 'flex-start',
  },
  publicationTeaserContainer: {
    width: '100%',
    height: '140px',
    borderRadius: '4px',
    boxShadow: '#00000024 0px 0px 4px 2px',
    fontFamily: 'Gravity',
  },
  publicationTeaser: {
    width: '100%',
    height: '100%',
    objectFit: "cover",
  },
  publicationTitle: {
    fontWeight: 800,
    fontSize: '16px',
    color: '#353535',
    fontFamily: 'Gravity',
  },
  publicationAuthors: {
    color: '#555555',
    fontFamily: 'Gravity',
  },
  publicationIconText: {
    fontSize: '16px',
    margin: '0 5px',
    position: "relative",
    top: '-6px',
    fontFamily: 'Gravity',
  }
});

type Props = {
  classes: {
    [propName: string]: string
  }
}

class Service extends Component<Props> {
  render() {
    const { classes } = this.props;
    const contactNode =
      <div>
        <Grid container spacing={6}>
          <Grid item md={8}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <div className={classes.subTitle}>{`# Service`}</div>
            </Typography>
          </Grid>
        </Grid>
        <Grid container spacing={4} justify="center" alignItems="center" className={classes.contentNode}>
          <Grid item md={1} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText2}>
              <MenuBookIcon />
            </Typography>
          </Grid>
          <Grid item md={11} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              TA in <i>Cross Media Visualization</i> and <i>Information Visualization</i>, 2020, ZJU
            </Typography>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              TA in <i>the International Visualization Summer School of ZJU</i>, 2019-2021, ZJU
            </Typography>
          </Grid>
          {/* <Grid item md={1} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="right" className={classes.contentText2}>
              <GradingIcon />
            </Typography>
          </Grid>
          <Grid item md={11} className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="left" className={classes.contentText2}>
              {`Reviewer of IEEE VIS, EuroVis, and ChinaVis`}
            </Typography>
          </Grid> */}
        </Grid>
      </div>
    return (
      <div className="service" id="service">
        {contactNode}
      </div>
    )
  }
}

export default withStyles(styles)(Service);
