import React, { Component } from 'react';
import '../App.css';
import { withStyles, createStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

const styles = createStyles({
  subTitle: {
    fontSize: '24px',
    fontWeight: 800,
    color: '#555555',
    textDecoration: 'none',
    fontFamily: 'Gravity',
    marginTop: '16px',
  },
  lifeImageContainer: {
    width: '100%',
    height: '90px',
    borderRadius: '4px',
    boxShadow: '#00000024 0px 0px 4px 2px',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  lifeImage: {
    width: '100%',
    height: '100%',
    objectFit: "cover",
    fontFamily: 'Gravity',
    userSelect: 'none',
    userDrag: 'none',
  },
  contentNode: {
    margin: '0 0 0px 0',
    justifyContent: 'flex-start',
  }
});

type Props = {
  classes: {
    [propName: string]: string
  }
}

class Life extends Component<Props> {
  state = {
    imageList: [
      {
        src: './life/bachelor.jpg',
        description: 'Graduate in ZJU'
      },

      {
        src: './life/diving.jpg',
        description: 'Diving in Hainan'
      },
      {
        src: './life/nature.jpg',
        description: 'The Tree and Me'
      },
      {
        src: './life/sunset.jpg',
        description: 'Sunset in Hangzhou'
      },
      {
        src: './life/hope.jpg',
        description: 'Hope grows out of the darkness.'
      },
      {
        src: './life/cooking.jpg',
        description: 'Self-made Delicious Noodles'
      },
      {
        src: './life/pingtan.jpg',
        description: 'Pingtan Beach'
      },
    ]
  }
  render() {
    const { classes } = this.props;
    const lifeNode =
      <div>
        <Grid container spacing={6}>
          <Grid item md={8}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <div className={classes.subTitle}>{`# Life`}</div>
            </Typography>
          </Grid>
        </Grid>
        <Grid container spacing={1} justify="center" alignItems="center" className={classes.contentNode}>
          {this.state.imageList.map((img) => <Grid item className={classes.contentText}>
            <Typography variant="subtitle1" gutterBottom align="right">
              <div className={classes.lifeImageContainer}>
                <img className={classes.lifeImage} src={img.src} alt={img.description} title={img.description}></img>
              </div>
            </Typography>
          </Grid>
          )}
        </Grid>

      </div>
    return (
      <div className="life" id="life">
        {lifeNode}
      </div>
    )
  }
}

export default withStyles(styles)(Life);
