import React, { Component } from 'react';
import '../App.css';
import { withStyles, createStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import ArticleIcon from '@mui/icons-material/Article';
import DvrIcon from '@mui/icons-material/Dvr';
import CampaignIcon from '@mui/icons-material/Campaign';
import MovieFilterIcon from '@mui/icons-material/MovieFilter';

const styles = createStyles({
  subTitle: {
    fontSize: '24px',
    fontWeight: 800,
    color: '#555555',
    textDecoration: 'none',
    fontFamily: 'Gravity',
    marginTop: '16px',
  },
  contentText1: {
    fontSize: '16px',
    lineHeight: 1.2,
    // paddingRight: '40px',
    fontFamily: 'Gravity',
  },
  contentText2: {
    fontSize: '18px',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  contentNode: {
    margin: '0 0 0px 0',
    fontFamily: 'Gravity',
  },
  publicationTeaserContainer: {
    width: '100%',
    height: '90px',
    borderRadius: '4px',
    boxShadow: '#00000024 0px 0px 4px 2px',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  publicationTeaser: {
    width: '100%',
    height: '100%',
    objectFit: "cover",
    fontFamily: 'Gravity',
    userSelect: 'none',
    userDrag: 'none',
  },
  publicationTitle: {
    fontWeight: 800,
    fontSize: '16px',
    color: '#353535',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
  publicationAuthors: {
    color: '#555555',
    lineHeight: 1.2,
    display: 'flex',
    flexWrap: 'wrap',
    fontFamily: 'Gravity',
    fontSize: '10px',
  },
  publicationIcons: {
    color: '#555555',
    lineHeight: 1,
    display: 'flex',
    flexWrap: 'wrap',
    fontFamily: 'Gravity',
  },
  publicationJournal: {
    color: '#555555',
    fontStyle: 'italic',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
    fontSize: '12px',
  },
  year: {
    color: '#555555',
    fontStyle: 'italic',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
    fontSize: '16px',
    marginTop: '10px'
  },
  publicationIconText: {
    fontSize: '12px',
    margin: '0 20px 0 0',
    position: "relative",
    top: '-6px',
    lineHeight: 1.2,
    fontFamily: 'Gravity',
  },
});

type Props = {
  classes: {
    [propName: string]: string
  }
}

class Publication extends Component<Props> {
  state = {
    publicationsByYear: [
      {
        year: 2022,
        publications: [
          {
            title: 'A Visualization Approach for Monitoring Order Processing in E-Commerce Warehouse',
            journal: 'IEEE Transactions on Visualization and Computer Graphics',
            conference: 'IEEE VIS',
            author: ['Junxiu Tang', 'Yuhua Zhou', 'Tan Tang', 'Di Weng', 'Boyang Xie', 'Lingyun Yu', 'Huaqiang Zhang', 'Yingcai Wu'],
            teaser: './publication/vis21-OrderMonitor.png',
            url: 'https://ieeexplore.ieee.org/document/9557224',
            website: '',
            otherlink: '',
            video: '',
            year: 2022,
          },
          {
            title: 'Nebula: A Coordinating Grammar of Graphics',
            journal: 'IEEE Transactions on Visualization and Computer Graphics',
            conference: '',
            author: ['Ran Chen', 'Xinhuan Shu', 'Jiahui Chen', 'Di Weng', 'Junxiu Tang', 'Siwei Fu', 'Yingcai Wu'],
            teaser: './publication/tvcg21-nebula.png',
            url: 'https://ieeexplore.ieee.org/abstract/document/9417674',
            website: '',
            otherlink: '',
            video: '',
            year: 2022,
          },
        ]
      },
      {
        year: 2021,
        publications: [
          {
            title: 'What Makes a Data-GIF Understandable?',
            journal: 'IEEE Transactions on Visualization and Computer Graphics',
            conference: 'IEEE InfoVis',
            author: ['Xinhuan Shu', 'Aoyu Wu', 'Junxiu Tang', 'Benjamin Bach', 'Yingcai Wu', 'Huamin Qu'],
            teaser: './publication/vis20-datagif.png',
            url: 'https://ieeexplore.ieee.org/abstract/document/9222309',
            website: 'https://data-gifs.github.io/',
            otherlink: '',
            video: 'https://www.youtube.com/watch?v=vs3iucWl0qI',
            year: 2021,
          },
          {
            title: 'Explore Mindfulness without Deflection: A Data Art Based on the Books of Songs',
            journal: '',
            conference: 'IEEE VIS Art Program - Pictorial',
            author: ['Yifang Wang', 'Yifan Cao', 'Junxiu Tang', 'Yang Wang', 'Huamin Qu', 'Yingcai Wu'],
            teaser: './publication/vis21-shijing.png',
            url: 'http://wangyifang.top/about/publication/paper-shijing-visap2021/visap2021-shijing-paper.pdf',
            website: 'http://wangyifang.top/the-book-of-songs/',
            otherlink: 'https://mp.weixin.qq.com/s/mMoqtZrJInwg02yGTLQ3TA',
            video: '',
            year: 2021,
          },
          {
            title: 'Examining Interaction Techniques in Data Visualization Authoring Tools from the Perspective of Goals and Human Cognition: A Survey',
            journal: 'Journal of Visualization',
            conference: '',
            author: ['Sadia Rubab', 'Junxiu Tang', 'Yingcai Wu'],
            teaser: './publication/jov21-survey.png',
            url: 'https://link.springer.com/article/10.1007/s12650-020-00705-3',
            website: '',
            otherlink: '',
            video: '',
            year: 2021,
          },
        ]
      },
      {
        year: 2020,
        publications: [
          {
            title: 'Narrative Transitions in Data Videos',
            journal: '',
            conference: 'IEEE VIS Short Paper',
            author: ['Junxiu Tang', 'Lingyun Yu', 'Tan Tang', 'Xinhuan Shu', 'Lu Ying', 'Yuhua Zhou', 'Peiran Ren', 'Yingcai Wu'],
            teaser: './publication/vis20-transition.png',
            url: 'https://ieeexplore.ieee.org/abstract/document/9331289',
            website: 'https://narrativetransitions.github.io/home/',
            otherlink: '',
            video: 'https://www.youtube.com/watch?v=RNFCuR9DgMg',
            year: 2020,
          },
          {
            title: 'SmartShots: Enabling Automatic Generation of Videos with Data Visualizations Embedded',
            journal: 'Proceedings of ACM International Conference on Multimedia',
            conference: 'ACM MM Abstract',
            author: ['Tan Tang', 'Junxiu Tang', 'Jiewen Lai', 'Lu Ying', 'Peiran Ren', 'Lingyun Yu', 'Yingcai Wu'],
            teaser: './publication/mm20-smartshots.png',
            url: 'https://dl.acm.org/doi/10.1145/3394171.3414356',
            website: '',
            otherlink: '',
            video: '',
            year: 2020,
          },
          {
            title: 'Design Guidelines for Augmenting Short-form Videos using Animated Data Visualizations',
            journal: 'Journal of Visualization',
            conference: 'China Vis',
            author: ['Tan Tang', 'Junxiu Tang', 'Jiayi Hong', 'Lingyun Yu', 'Peiran Ren', 'Yingcai Wu'],
            teaser: './publication/jov20-guideline.png',
            url: 'http://link.springer.com/article/10.1007/s12650-020-00644-z',
            website: '',
            otherlink: '',
            video: '',
            year: 2020,
          },
        ]
      },
      {
        year: 'Before 2019',
        publications: [
          {
            title: 'Toward the Better Modeling and Visualization of Uncertainty for Streaming Data',
            journal: 'Journal of Visualization',
            conference: 'China Vis',
            author: ['Tan Tang', 'Kaijuan Yuan', 'Junxiu Tang', 'Yingcai Wu'],
            teaser: './publication/jov19-uncertainty.png',
            url: 'https://link.springer.com/article/10.1007/s12650-018-0518-y',
            website: '',
            otherlink: '',
            video: '',
            year: 2018,
          },
        ]
      },
    ]
  }

  render() {
    const { classes } = this.props;
    const { publicationsByYear } = this.state;

    const publicationNode =
      <div>
        <Grid container spacing={2}>
          <Grid item md={8}>
            <Typography variant="h2" component="h2" gutterBottom align="left">
              <div className={classes.subTitle}>{`# Publication`}</div>
            </Typography>
          </Grid>
        </Grid>
        {publicationsByYear.map((pubByYr) =>
          pubByYr.publications.length > 0 ?
            <div>
              <Typography variant="subtitle1" gutterBottom align="left" className={classes.year}>
                {pubByYr.year}
              </Typography>
              {pubByYr.publications.map((pub) =>
                <Grid container spacing={4} justify="center" alignItems="center" className={classes.contentNode}>
                  <Grid item md={3}>
                    <div className={classes.publicationTeaserContainer}>
                      <img alt={pub.title} src={pub.teaser} className={classes.publicationTeaser} />
                    </div>
                  </Grid>
                  <Grid item md={9}>
                    <Typography variant="subtitle1" gutterBottom align="left" className={classes.publicationTitle}>
                      {pub.title}
                    </Typography>
                    <Typography variant="subtitle1" gutterBottom align="left" className={classes.publicationJournal}>
                      {pub.journal === "" ? `${pub.conference}. ${pub.year}.` :
                        `${pub.journal}. ${pub.year}. ${pub.conference === "" ? "" : (`(${pub.conference})`)}`}
                    </Typography>
                    <Typography variant="subtitle1" gutterBottom align="left" className={classes.publicationAuthors}>
                      {pub.author.map((a, i) => {
                        const text = `${a}${i === pub.author.length - 1 ? '' : ', '}`;
                        return a === "Junxiu Tang" ?
                        <div style={{ fontWeight: 800, marginLeft: `${ i === 0 ? 0 : 4}px` }}>{text}</div> :
                        <div style={{ marginLeft: `${ i === 0 ? 0 : 4}px` }}>{text}</div>
                      })}
                    </Typography>
                    <Typography variant="caption" gutterBottom align="left" className={classes.publicationIcons}>
                      <Grid item md={6}>
                        {pub.url === "" ? null : (<React.Fragment><a href={pub.url}><ArticleIcon fontSize='small' /> <span className={classes.publicationIconText}>paper</span></a></React.Fragment>)}
                        {pub.website === "" ? null : (<React.Fragment><a href={pub.website}><DvrIcon fontSize='small' /> <span className={classes.publicationIconText}>page</span></a></React.Fragment>)}
                        {pub.otherlink === "" ? null : (<React.Fragment><a href={pub.otherlink}><CampaignIcon fontSize='small' /> <span className={classes.publicationIconText}>blog</span></a></React.Fragment>)}
                        {pub.video === "" ? null : (<React.Fragment><a href={pub.video}><MovieFilterIcon fontSize='small' /> <span className={classes.publicationIconText}>video</span></a></React.Fragment>)}
                      </Grid>
                    </Typography>
                  </Grid>
                </Grid>
              )}
            </div>

            : null
        )}
      </div>
    return (
      <div className="publication" id="publication">
        {publicationNode}
      </div>
    )
  }
}

export default withStyles(styles)(Publication);
